<template>
	<div ref="provincChart" class="echart-box"></div>
</template>


<script>
	export default {
		name: 'Province',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawChart(map) {
				let chart = this.$echarts.getInstanceByDom(this.$refs.provincChart);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.provincChart, 'light');
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				if (!this.$echarts.getMap('China')) {
					this.$echarts.registerMap('China', map)
				}
				let option = {
					title: {
						text: '各省份数据量对比',
						left: 'center'
					},
					tooltip: {},
					visualMap: {
						min: 1612000,
						max: 1618000,
						text: ['High', 'Low'],
						realtime: false,
						calculable: true,
						inRange: {
							color: ['#74e3e5', '#ffffbf', '#ff9f7f']
						}
					},
					series: [{
						name: "数据量",
						map: "China",
						type: "map",
						aspectScale: 1.0,
						selectedMode: 'single',
						hoverable: false,
						roam: true,
						itemStyle: {
							normal: {
								borderWidth: 1,
								borderColor: '#ffffff',
								areaColor: '#FFDAB9',
								label: {
									show: true,
									textStyle: {
										color: '#6495ED',
										fontSize: 10
									}
								}
							},
							emphasis: { // 选中样式
								borderWidth: 1,
								borderColor: '#00ffff',
								color: '#ffffff',
								areaColor: '#FFDAB9',
								label: {
									show: true,
									textStyle: {
										color: '#ff0000'
									}
								}
							}
						},
						data: this.list
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'data/province.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					let name = resp.data[i][0];
					let value = resp.data[i][1];
					this.list.push({
						name: name,
						value: value
					})
				}
				this.$axios.get(process.env.BASE_URL + 'map/china.json').then(resp => {
					this.drawChart(resp.data);
				})
			})
		}
	}
</script>

